<!-- AppBar ( ''AppBar ) -->
<template>
    <mu-appbar class="auto__AppBar" >
        <mu-icon-button icon='list' slot="left" @click="toggle(true)" />
        <mu-text-field icon="search" class="auto__AppBar-search-field"  slot="right" />
        <mu-icon-button icon='chat' slot="right" />
        <!--  -->
        <mu-drawer :open="open" :docked="docked" class="left--box" @close="toggle()">
            <mu-list @itemClick="docked ? '' : toggle()">
                <mu-list-item title="" @click="toAboutInfo( 'newFeatures' )" />                             <!-- NewFeatures -->
                <mu-list-item title=""   @click="toAboutInfo( 'FAQ' )" />                                     <!-- FAQ -->
                <mu-list-item title=""   @click="toAboutInfo( 'aboutUs' )" />                                 <!-- AboutUs -->
                <mu-list-item title=""   @click="toAboutInfo( 'cooperation' )" />                             <!-- Cooperation -->
                <mu-list-item title=""   @click="toAboutInfo( 'privacyPolicy' )" />                           <!-- PrivacyPolicy -->
                <mu-list-item title=""   @click="toAboutInfo( 'copyrightInfo' )" />                           <!-- CopyrightInfo -->
            </mu-list>
        </mu-drawer>
    </mu-appbar>
</template>
<script>
export default {
    data () {
        return {
            open: false,
            docked: true
        }
    },
    mounted: function () {
        this.setListObj_marginTop()
    },
    methods: {
        toggle (flag) {
            this.open = !this.open
            this.docked = !flag
        },
        toAboutInfo: function(pageName) {
            location.href = '#/aboutAPlus/' + pageName
        }
        // :   
        ,setListObj_marginTop: function() {
            setTimeout(() => {
                const appBar_height     = document.getElementsByClassName("auto__AppBar")[0].offsetHeight       // AppBar
                const tabs_height       = document.getElementById("building_tabs").offsetHeight                 // tabs
                const buildingList_Obj  = document.getElementsByClassName("buildingList")[0]                    // 
                buildingList_Obj.style.marginTop = appBar_height + tabs_height + 18 + "px"                      //   margin-top( $autoMargin )
            }, 100)
        }
    }
}
</script>
<style lang="sass">
@import '../../sass/main'

.auto__AppBar-search-field
    color: #FFF
    margin-bottom: 0
    &.focus-state
        color: #FFF
    .mu-text-field-hint
        color: fade(#FFF,54%)
    .mu-text-field-input
        color: $title-color-light
    .mu-text-field-focus-line
        background-color: #FFF
// AppBar ( ''AppBar )
.auto__AppBar
    position: fixed
    top: 0
    +bC(rgba($F,.1))
    opacity: 1
    .mu-icon                                                                      // 
        color: $theme-color !important
    .mu-appbar-title
        width: 0 !important
    .left
        position: absolute                                                          // (  )
        left: 0
        width: 14%
    .right
        width: 84%
        margin-left: -3%
        .mu-text-field                                                              // (  )
            position: absolute
            left: 15%
            padding-left: 38%!important
            margin-bottom: 0 !important
            >.mu-text-field-icon                                                    // 
                left: 44.5% !important
                top: 25% !important
            >div>div
                .mu-text-field-line,.mu-text-field-focus-line
                    left: 0 !important
        .mu-icon-button                                                             // (  )
            position: absolute
            right: 0
        .mu-text-field.has-icon
            padding-left: 0 !important                                          // 
        .focus-state
            >i.mu-icon
                display: none !important
    //  (  )
    .mu-text-field-line
        +bC($badgeFont-color)
    //  (  )
    .mu-text-field-focus-line
        +bC($theme-color)
</style>



